/*-imported stuff--*/



*{
    padding: 0;
    margin: 0;
    font-size: 62.4%;
    box-sizing:border-box;
}



.logo{
    
    align-items: center;
    display:flex;


}
.link-logo{
    text-decoration: none;
    font-weight: 900;
    font-size: 1.5rem;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: #3b3b3b;
}


.a-tag{
     padding: 1rem;
}

#root{
    height:100%;
}
body{
    min-height:100vh;
    font-family: sans-serif;
    position:relative;
    padding-bottom: 10rem;
  
}

main{
    

     padding-top: 20px;
      padding-bottom: 50px ;
  
      margin-bottom: 20px;

    }

/*----------------------
-----Product Grid------
------------------------*/


.product-grid{
 
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    
   
}

.card {
    display: flex;
   
    flex-direction: column;
    font-size: 3rem;
    /* Add shadows to create the "card" effect */
    overflow: hidden;
    background-color: white;
    height: 400px;
    width:   300px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    margin: 10px 20px;
    
  }


  .card ,.cart{
      height: 35rem;

  }

  .card ul{
      
      list-style-type: none;
      padding:5px 2px;
      position: relative;

  }


  .card ul li{
      padding: 8px;
      font-size: 2rem;
      font-weight: 200;
  }
  
  
  .card ul li img{
      height:240px;
      width:100%;
  }

  .CartIcon{
      outline: none;
     background: none;
     border: none;
     position: absolute;
     
     right: 10px;
    bottom: 5px;
    padding: 10px;
    width: 40px;
    height: 40px;
    
  
  }

  .CartIcon:hover{
    background: whitesmoke;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    text-align: center;
    line-height: 20px;
    vertical-align: middle;
    padding: 10px;
    

  }



  .faCartIcon{
      font-size: 2rem;
      color:#3b3b3b;
  }



  .prod-desc{
      font-size: 1.5rem;
       font-weight: 700;
  }

  .product-name-price{
      font-size: 3rem;
      font-weight: 600;
    display:flex;
    justify-content: space-between;


  }

  /* On mouse-over, add a deeper shadow */
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

/*----------------------
-----NavBar------
------------------------*/



.navbar {
    
    align-items: center;
    display: flex;
    font-size: 3rem;
    height: 4rem;
   
}
hr{
    width: 100%;
}

nav{
     line-height: 2rem;
    display: flex;
    width:100%;
    justify-content: space-between;
    font-size: 3rem;
    align-items: center;
    
}

.logo-img{
    height:40px;
    width:40px;
    overflow: hidden;
}


.icon-cart{
    font-size: 2rem;
   color:gray;


}

h1{
    font-size: 2.5rem;
    font-weight: 900;
     text-align: center;

}

h3{
    font-size: 2rem;
}

.cart-main{ 
   
    padding: 10px 0px;
    display: flex;
    
    

}


.dropdown-opt{
      
    width: 60%;
}

.qty{
    display:flex;
     
    height:10px;
    width: 30%;
    font-size: 1.6rem;
    align-items: center;
}

h4{
    font-size: 1rem;
}

.qty button{
    margin: 0 1em;
    height: 20px;
    width: 20px;
    border: none;
    vertical-align: middle;

}

.subTotal{
    
    padding:10px;
    border: 1px solid rgb(226, 210, 210);
 margin: 20px auto;
    width: 70%;
    display: flex;
    flex-direction: column;
    font-size: 3rem;

}

/*-------------------------------
-----------------Buttons---------
--------------*/
.link{
    text-decoration: none;
   color: white;
   font-size: 1.6rem;
   font-weight: 200 ;

}
.emptyCart-btn , .checkOut-btn{
    font-size: 1rem;
    width: 100%;
    background-color: #131335;
    color: white;
    font-weight: 200 ;
    opacity: 1;
    border: none;
}

.remove-btn{
    height: 20px;
    width: 60px;
  

    overflow: hidden;
    background-color: #131335;
    border-radius: 3px;
    color: white;
    border: none;
    outline: none;
    
    font-size:1rem;
   


}

.df{
    background-color: red;
    width: 50%;
    height: 50%;
}



  /*--------------------------
  -----------FORM -------------*/


  .form-contianer{
    display: flex;
 
  }

  .form-feild {
       margin: 10px;
       padding: 2rem;
     
  }

 

  input{
    
      width: 100%;
      border: none;
      border-bottom: 1px solid black;
     
      height: 2rem;
      outline: none;
      font-size: 1.8rem;
      margin: 1.6rem 0;

  }
  h3{
      font-size: 1.8rem;
  }


.countries-option{
    font-size:2rem;
    width: 80%;


  }

  .flex-column{
      border: 1px solid black;
    
    display:flex;
    flex-direction: column;
    padding: 1rem;
   
  }

  .orderList{
        min-width:  200px;
        display:flex;
        flex-direction: column;
        font-size: 1.5rem;
        border: 1px solid rgb(226, 210, 210);
        padding: 2rem;
        border-radius: 1rem;
        margin: 1rem;
           
 }

 
 
 .jc-sb{
     font-size: 2.5rem;
     display: flex;
     flex-direction: row;
     justify-content: space-between;
     padding: 2rem 0;
 }

 .itemName, .price{
     font-weight: 800;
    
 }

 .qty{
     font-size: 1.5rem;
     min-width: 200px;
     color: #000; /* Fallback for older browsers */
     color: rgba(0, 0, 0, 0.5);
 
 }

 .chk-subtotal{
     font-size: 2rem;
     font-weight: 800;
 }



 .flex-jc{
    display: flex;
    
    justify-content: space-between;

 }
 
 .sub-btn {
    display: flex;
 
    justify-content: space-between;
    text-align: center;
    padding: 1rem 0px

    

 }


 .chkout-btn{
     
    width:  50%;
   
        font-size: 1rem;
        font-family: 700;
        color: white;
        background-color: #131335;
        border: none;
        outline: none;
        text-decoration: none;
         margin: 0 3rem;
         text-align: center;
         padding: 3px 5px;
 

 }

 .sub-btn button{
        width: 50%;

 }


 .payment-form{
       padding-bottom: 2rem ;



 }



 .footer{
    position: fixed;
    bottom: 0;
    height: 5rem;
    display: flex;
    font-size: 2rem;
    align-items: center;
    justify-content: space-between;
    width:100%;
     border-top: 1px solid rgb(44, 42, 42);
        background-color: black;


 }


 .copyright{
     padding: 2rem;
     color: white;
     font-size: 1.5rem;
     font-weight: 300;
 }

.footer-links ul{
    display: flex;
    padding: 2rem; 
    width:50%;
    flex-direction: row;


   
    font-size: 3rem;

}

.footer-links li{
    padding: 0 1rem;
    list-style-type: none;
}

 .social-icons-bg{
    border: none;
    outline: none;
     font-size: 2rem;
     background-color: black;
     color: white;
     
     
 }

 @media only screen and (min-width: 250px){


        .copyright{
           
            font-size: 9px;
        }

        
.footer-links ul{
    display: flex;
    padding: 5px; 
    width:40%;
    flex-direction: row;


   
    font-size: 3rem;

}
        .footer-links li{
            padding: 0 5px;
            list-style-type: none;
        }
 };
     
 

 
 @media only screen and (max-width: 450px){
    .copyright{
        font-size: 1.7rem;
    }

    .social-icons-bg{
        font-size: 1.7rem;
    }


   .card, .cart{
       margin: 4px;

   }

   .sub-btn{
       font-size: 1rem;
   }

 }

